<template>
  <div class="summon">
    <div class="container" id="innerS">

        <hr class="layui-border-cyan">

        <div class="row">
          <button @click="init" class="layui-btn layui-btn-danger">重置</button>
        </div>

        <hr class="layui-border-cyan">

        <div class="row">
          <button id="sum" @click.stop="once" class="layui-btn layui-btn-primary">召唤一次</button>
          <button id="sumAll" @click.stop="all" class="layui-btn">十连召唤</button>
        </div>

        <hr class="layui-border-cyan">

        <div id="upSelect" class="row">
          <h3>当前UP:</h3>
          <img :src="upRole.url" class="img-responsive center-block">
          {{upRole.name}}
          <select class="form-control" v-model="upRole">
            <option v-for="(up, index) in upArray" :key="index" :value="up">{{up.name}}</option>
          </select>
        </div>

        <hr class="layui-border-cyan">

        <div class="result">

          <div v-if="loadingShow"><i class="layui-icon layui-icon-loading layui-anim layui-anim-rotate layui-anim-loop" style="font-size: 50px"></i></div>

          <div class="row">
            <img :src="result.url"><br>
            <span>{{result.name}}</span>
          </div>

          <div class="row">
            <div id="resDiv" class="col-md-2" style="display: inline-block;" v-for="(res, index) in result" :key="index">
              <span class="imgBox">
                <img :src="res.url" class="resImg">
              </span>
              <div>{{res.name}}</div>
              
            </div>
          </div>

        </div>

        <hr class="layui-border-cyan">

        <div id="probability" class="row">
            <div class="row">抽取次数:{{clickCount}}</div>
            <div class="row" style="background-color: #FBED72">3星概率:{{Probability.three}}</div>
            <div class="row" style="background-color: #9EDAEE">2星概率:{{Probability.two}}</div>
            <div class="row" style="background-color: #EF9265">1星概率:{{Probability.one}}</div>
        </div>

      </div>
  </div>
</template>

<script>
import {onceSum, allIn, get3XRole, get2XRole, get1XRole} from '@/components/choujiangComponents/summonJs/main'

export default {
  name: 'Summon',
  data() {
    return {
      loadingShow: false,
      upArray: [],//3星
      tArray: [],//2星
      oArray: [],//1星
      upRole: {name: '无', url: ''},
      result: [],
      resArray: {
        three: 0,
        two: 0,
        one: 0
      },
      clickCount: 0
    }
  },
  methods: {
    once() {
      this.loadingShow = !this.loadingShow
      document.getElementById('sum').className = 'layui-btn layui-btn-disabled'
      document.getElementById('sumAll').className = 'layui-btn layui-btn-disabled'

      if(this.loadingShow == true) {
        setTimeout(() => {
          this.loadingShow = false
          this.result = onceSum(this.upRole)
          this.clickCount++

          for(let i of this.upArray) {
            if(i.name == this.result.name) {
              this.resArray.three++
            }
          }
          for(let i of this.tArray) {
            if(i.name == this.result.name) {
              this.resArray.two++
            }
          }
          for(let i of this.oArray) {
            if(i.name == this.result.name) {
              this.resArray.one++
            }
          }
          document.getElementById('sum').className = 'layui-btn layui-btn-primary'
          document.getElementById('sumAll').className = 'layui-btn'
        }, 2000);
      }else{
        this.loadingShow = !this.loadingShow
      }

    },
    all() {
      this.loadingShow = !this.loadingShow
      document.getElementById('sum').className = 'layui-btn layui-btn-disabled'
      document.getElementById('sumAll').className = 'layui-btn layui-btn-disabled'

      if(this.loadingShow == true) {
        setTimeout(() => {
          this.loadingShow = false
          this.result = allIn(this.upRole)
          this.clickCount += 10
          for(let a of this.result) {

            for(let i of this.upArray) {
              if(i.name == a.name) {
                this.resArray.three++
              }
            }
            for(let i of this.tArray) {
              if(i.name == a.name) {
                this.resArray.two++
              }
            }
            for(let i of this.oArray) {
              if(i.name == a.name) {
                this.resArray.one++
              }
            }

          }
          document.getElementById('sum').className = 'layui-btn layui-btn-primary'
          document.getElementById('sumAll').className = 'layui-btn'
        }, 2000);
      }else{
        this.loadingShow = !this.loadingShow
      }

    },
    init() {
      this.upRole = {name: '无', url: ''};
      this.result = [];
      this.resArray = {three: 0, two: 0, one: 0};
      this.clickCount = 0
    }

  },computed: {
    Probability() {
      let threeP =  this.resArray.three/this.clickCount * 100
      let twoP =  this.resArray.two/this.clickCount * 100
      let oneP =  this.resArray.one/this.clickCount * 100
      return {
        three: threeP.toFixed(2) + '%',
        two: twoP.toFixed(2) + '%',
        one: oneP.toFixed(2) + '%',
      }
    }
  },
  created() {
    this.upArray = get3XRole()
    this.tArray = get2XRole()
    this.oArray = get1XRole()
  }
}
</script>

<style>
/* body {
  background:url('http://img.netbian.com/file/2017/1205/dc608723216504c55420b9f72a75e506.jpg')no-repeat;
  background-color: #121D31;
  width:100%;
  height:100%;
  background-size: 100%;
  position:absolute;
  
} */

#innerS {
  background: #FFFFFF;
  border-radius: 10px;
  /* padding-right: 2%; */
  text-align: center;
  opacity: 0.8;
}

#upSelect {
  padding: 0% 35% 0% 35%;
}

#probability {
  padding: 0% 25% 0% 25%;
}

#resDiv {
  margin: 0% 1% 2% 1%;
}

.imgBox {
  display: inline-block;
  width: 64px;
  height: 72px;
}

.resImg {
  /* border-radius: 5px; */
  object-fit: fill;
  width: 100%;
  height: 100%;
}
</style>