//3星列表
export function get3XRole() {
  return array3X
}
export function get2XRole() {
  return array2X
}
export function get1XRole() {
  return array1X
}

//召唤函数
function summon(up) {
  //up角色
  let upRole = up

  var n = getRandom(1,100000)
  var result
  if(n < 2750){
      if(upRole.name == '无'){
          var child_n = getRandom(1,array3X.length)
          result = array3X[child_n-1]
      }else{
          if(n < 1375){
            result = upRole
          }else{
              while(true){
                  var child_n = getRandom(1,array3X.length)
                  result = array3X[child_n-1]
                  if(result.name !== upRole.name){
                      break
                  }
              }
          }
      }
  }else if(n>=2750 && n<21750){
      var child_n = getRandom(1,array2X.length)
      result = array2X[child_n-1]
  }else{
    var child_n = getRandom(1,array1X.length)
    result = array1X[child_n-1]
  }
  return result
}

export function onceSum(up) {
  return summon(up)
}
export function allIn(up) {
  let result = []
  for(let i = 1 ; i <= 10 ; i++){
    let onceResult = summon(up)
    result.push(onceResult)
  }
  return result
}